*{
    margin: 0;
}
.body{
    box-sizing: border-box;
}
.bg-container{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #F0F8FF;
    max-width: 100vw;
    height: 100vh;
    
}
@media (min-width: 768px){
    .bg-container{
        padding-left: 200px;
    }
}

.heading{
    font-family: sans-serif;
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 10px;
}
@media (max-width: 768px) {
    .heading{
        font-size: 25px;
        margin-left: 10px;
    }
    .head{
        
        font-size: 18px;
        font-weight: 500;
    }
}
.error{
    font-family:'Special Elite', cursive;
    font-weight: 500;
    color: red;
    margin-bottom: 30px;
}
.scanner{
    display: flex;
    flex-direction: row;
}
.head{
    font-family: 'Special Elite', cursive;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 2px;
}
    
